.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
}
.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}
.footer-logo img{
    width: 100px;
    height: 40px;
    margin-right: 10px;
}
.footer-logo h2{
    font-size: 40px;
    font-weight: 600;
    
}
.footer-links{
    display: flex;
    list-style-type: none;
    font-size: 18px;
    font-weight: 500;
    gap: 50px;
}
.footer-links li{
    cursor: pointer;
}
.footer-social-icons{
    display: flex;
    gap: 20px;
    
}
.footer-icons-container img{
    width: 40px;
    height: 40px;
    /* border: 1px solid black; */
    box-shadow: inset ;

}
.footer-icons-container{
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid rgb(224, 221, 221);
    border-radius: 20%;

}
.footer-copyright{
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 30px;
   width: 100%;
   margin-bottom: 30px;
   color: #1a1a1a;
   font-size: 20px;
}
.footer-copyright hr{
    width: 80%;
    height: 3px;
    border: none;
    border-radius: 10px;
    background: #c7c7c7;
}