.new-collections {
display: flex;
flex-direction: column;
align-items: center;
gap: 10px;
/* height: 90vh; */
margin-bottom: 100px;
}
.new-collections hl{
color: #171717;
font-size: 50px;
font-weight: 600;
}
.new-collections hr{
width: 200px;
height: 6px;
border-radius:10px;
background: #252525;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 30px;
    gap: 30px;

}

